import React from "react"

export default () =>
<div>
    <div className="jumbotron connect">
        <div className="container">
            <h1>Play Return of the Shadow!</h1>
            <p>Connect at rotsmud.dyndns.org port 3791</p>
        </div>
    </div>
    <section className="play">
        <div className="container">
            <div className="row">
                <div className="col-lg-12">
                    <h1>From your browser</h1>
                    <p>The web client is the simplest option, recommended if you want to get a quick feel of the game. Also, it should work with most corporate/school networks, whereas MUD clients (see below) may not.</p>
                </div>
            </div>
        </div>
    </section>
    <section className="offwhite">
        <div className="container">
            <div className="row">
                <div className="col-lg-12">
                    <h1>With a MUD Client</h1>
                    <p>This is the most powerful option, but it may require installing and configuring software on your comptuer.</p>
                    <p>If you already have a MUD client, just connect to <strong>rotsmud.dyndns.org</strong> on port <strong>3791</strong> and you are ready to go. Check out the newbie section for a first advice what to do in the game.</p>
                </div>
            </div>
        </div>
    </section>
</div>


